<template>
  <div id="app">

        <nav class="navbar fixed-top bg-dark">
      <a class="navbar-brand" href="#">
        <img
          src="https://www.sykes.com/wp-content/uploads/2020/01/sykes-logo-nav.svg"
          width="100"
          height="30"
          class="d-inline-block align-top"
          alt=""
        />
      </a>
    </nav>
    <b-container >
      <b-row>
        <b-col class="panel col-8 col-centered py-2">
          <h1 class="pt-4 pb-2 text-center">New Account</h1>
            <div>
              <b-form @submit="onSubmit" @reset="onReset" v-if="show">
                <div v-if="!success">
                  <b-form-group id="input-group-first-name" label="First Name:" label-for="input-first-name" class="py-3">
                    <b-form-input
                      id="input-first-name"
                      v-model="firstName"
                      placeholder="Enter first name"
                      required
                    ></b-form-input>
                  </b-form-group>

                    <b-form-group id="input-group-last-name" label="Last Name:" label-for="input-last-name" class="py-3">
                    <b-form-input
                      id="input-last-name"
                      v-model="lastName"
                      placeholder="Enter last name"
                      required
                    ></b-form-input>
                  </b-form-group>
                 
                  <b-form-group
                    id="input-group-email"
                    label="Email address:"
                    label-for="input-email"
                    class="py-3"
                  >
                    <b-form-input
                      id="input-email"
                      v-model="email"
                      type="email"
                      placeholder="Enter email"
                      required
                    ></b-form-input>
                  </b-form-group>

                  <b-form-group
                    id="input-group-username"
                    label="Username:"
                    label-for="input-username"
                    class="py-3"
                  >
                    <b-form-input
                      id="input-username"
                      v-model="username"
                      placeholder="Enter username"
                      required
                    ></b-form-input>
                  </b-form-group>
        
                  <b-form-group
                    id="input-group-age"
                    label="Age:"
                    label-for="age"
                    class="py-3"
                  >
                    <b-form-input
                      id="input-age"
                      v-model="age"
                      placeholder="Enter age"
                      required
                    ></b-form-input>
                    <b-form-invalid-feedback :state="ageState">
                      Invalid age for new account
                    </b-form-invalid-feedback>
                  </b-form-group>

                  <b-form-group id="input-group-gender" label="Gender:">
                      <b-form-radio v-model="gender" value="male" id="rb-male">Male</b-form-radio>
                      <b-form-radio v-model="gender" value="female" id="rb-female">Female</b-form-radio>
                      <b-form-invalid-feedback :state="rbState">Please select one</b-form-invalid-feedback>
                  
                  </b-form-group>

                  <div class="float-end p-2">
                  <b-button type="submit" variant="success" size="lg">Submit</b-button>
                  </div>
                </div>
                <div v-if="success" class ="p-3">
                  <h3>Welcome!</h3>
                  <h5 class="py-3">Here is your account info:</h5>
                  <b-row class="p-3">
                  <b-col cols=4>
                    <div class="bold">
                      Member ID:
                    </div>
                  </b-col>
                  <b-col id="member-id">
                      {{memberId}}
                  </b-col>
                  </b-row>
                  <b-row class="p-3">
                  <b-col cols=4>
                    <div class="bold">
                      Temporary Password:
                    </div>
                  </b-col>
                  <b-col id="temp-password">
                      {{tempPassword}}
                  </b-col>
                  </b-row>
                    <b-button type="reset" variant="danger" size="lg" class="float-end">Reset</b-button>
                   
                </div>
              </b-form>
            </div>
        </b-col>
      </b-row>
    </b-container>    
  </div>
</template>

<script>

export default {
  name: 'App',

   data() {
      return {
        firstName:'',
        lastName:'',
        email:'',
        username:'',
        age: '',
        gender: '',
        show: true,
        success: false,
        ageState: null,
        rbState: null,
        memberId: '',
        tempPassword: ''
      }
    },
    methods: {
      onSubmit(event) {
        event.preventDefault()
        this.memberId = this.generateMemId()
        this.tempPassword = this.generatePassword()
        this.success = true
      
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.firstName = ''
        this.lastName = ''
        this.email = ''
        this.username = ''
        this.age = ''
        this.gender = ''
        this.rbState = null
        this.success = false
        this.memberId = ''
        this.tempPassword = ''
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },
      generateMemId(){
        var now = new Date()
        return "" + now.getMilliseconds() + now.getMinutes() +now.getSeconds()
      },
      generatePassword(){
        var now = new Date()

        var temp = "" + now.getMilliseconds() + now.getSeconds() + now.getMinutes() + now.getHours()

        return btoa(temp)
      }
    }   
}
</script>

